@import url(https://fonts.googleapis.com/css?family=Quicksand);
@import url(https://fonts.googleapis.com/css?family=Oxanium:500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 1;
  box-sizing: border-box;
}

body {
  font-size: 92%;
  font-family: 'Quicksand', sans-serif;
  color: white;
  background: #1A2980;  
  background: -webkit-gradient(linear, left top, right top, from(#26D0CE), to(#1A2980));  
  background: linear-gradient(to right, #26D0CE, #1A2980); 
}
h2, h3, h4, h5 {
  margin: 0 0 3px;
}


.listOfCards {
  display: inline;
  flex-wrap: wrap;
}

.UserCard {  
  display: inline-block;
  width: 290px;
  height: 360px;
  border-radius: 10px;
  box-shadow: 5px 5px 30px black;
  margin: 10px;
  background: #0c2b5e; 
  background: -webkit-gradient(linear, right top, left top, from(#0c2b5e), to(#173c77)); 
  background: linear-gradient(to left, #0c2b5e, #173c77);
}

.UserCard img {

  width: 200px;
  height: 200px;
  margin: 20px 0 0 0;
  border-radius: 50%;
  border: 4px solid white;
  object-fit: cover;
}

.UserCardTop {
  height: 67%;
  width: 100%;
  overflow: hidden;
  text-align: center;
}

.UserCardBottom {
  min-height: 40%;
  overflow: auto;
  min-width: 100%;
  max-width: 100%;
  padding: 0 10px 5px;
  overflow-wrap: break-word;
}

.UserCardBottom::-webkit-scrollbar {width: 3px; height:1px;-webkit-transition:all .5s;transition:all .5s;z-index:10;}
.UserCardBottom::-webkit-scrollbar-track {background-color: #0c2b5e;}
.UserCardBottom::-webkit-scrollbar-thumb {
  background-color: #bec4c8; 
  border-radius:3px;
}


body, html {
    height: 100vh;
    text-align: center;
 
    margin: 0;
    scroll-behavior: smooth;
}

.header-image {
    height: 100vh;
    
}

.header-text {
    text-align: center;
    position: absolute;
    font-size: 300%;
    top: 50%;
    left:50%;
    font-family: 'Oxanium';
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    color: black;
}

.subhead {
    font-size: 300%;
    color: black;
    font-family: 'Oxanium';
}
